<template>
  <div class="home">
    <VaAlert
      color="primary"
      border="top"
      border-color="primary"
      class="mb-6 alert-full-width"
    >
      Chatting Room
    </VaAlert>
    <div class="chat-container">
      <!-- „ÉÅ„É£„ÉÉ„Éà„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆ„É™„Çπ„Éà„Å®„É°„ÉÉ„Çª„Éº„Ç∏ÂÖ•Âäõ„Éï„Ç£„Éº„É´„Éâ„ÇíÂåÖÂê´„Åô„Çã„Ç≥„É≥„ÉÜ„Éä -->
      <div class="chat-section">
        <message-list
          :messages="messages"
          @quote="quoteMessage"
          @update-messages="updateMessages"
        ></message-list>
        <message-input :value="inputMessage" @send="addMessage"></message-input>
      </div>
      <!-- „Éó„É≠„É≥„Éó„ÉàË°®Á§∫ÈÉ®ÂàÜ -->
      <prompt-display></prompt-display>
    </div>
  </div>
</template>

<script>
// import dummyData from '@/dummyData';
import MessageList from "@/components/MessageList.vue";
// import MessageInput from "@/components/MessageInput.vue";
import PromptDisplay from "@/components/PromptDisplay.vue";
import axios from "axios";

export default {
  name: "HomePage",
  // props: ["id"],
  components: {
    MessageList,
    PromptDisplay,
  },
  data() {
    return {
      inputMessage: "",
    };
  },
  mounted() {
    const csrfToken = this.getCookie("csrftoken");
    axios
      .post(
        process.env.VUE_APP_BASE_URL + "create_default_room/",
        {
          room_name: "HomePage",
          room_id: 0,
        },
        {
          headers: {
            "X-CSRFToken": csrfToken.value,
          },
        }
      )
      .then((response) => {
        console.log(response.data);
        this.$store.commit("setCurrentRoom", 0);
        this.$store.dispatch("fetchMessages");
        this.$store.dispatch("fetchCurrentUser").then((currentUser) => {
          console.log("Current user:", currentUser);
          if (currentUser) {
            this.$store.dispatch("fetchIconColor");
          } else {
            console.error("Current user is null");
          }
        });
      })
      .catch((error) => {
        console.error(error);
      });
  },
  computed: {
    messages() {
      return this.$store.state.messages;
    },
    currentRoom() {
      return this.$store.state.currentRoom;
    },
    currentUser() {
      return this.$strore.state.currentUser;
    },
  },

  /*created() {
    this.$store.dispatch("fetchIconColor");
  }, */
  methods: {
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
    addMessage() {
      if (!this.currentRoom) {
        console.error("currentRoom is not set");
        return;
      }
      console.log(this.inputMessage);
      const messageData = this.inputMessage;
      this.$store.dispatch("addMessage", messageData);
      this.inputMessage = "";
    },
    quoteMessage(message) {
      this.inputMessage = `> ${message}\n`;
    },
    updateMessages(newMessages) {
      this.$store.commit("SET_MESSAGES", newMessages);
    },
  },
};
</script>

